.event {
    padding: 0.75rem;

    display: flex;
    flex-direction: column;
    gap: 5px;

    background-color: var(--bg-color);
    border: 1px solid var(--input-border);
    border-radius: 8px;
}

.event-name {
    padding-bottom: 5px;

    border-bottom: 1px solid var(--input-border);

    font-weight: 700;
    color: var(--primary-color);
}

.rewards {
    padding: 8px 12px;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 7px;

    width: 100%;

    background: #fff;
    border: 1px solid #e0e0e0;
    border-radius: 6px;

    font-size: 0.9rem;
    word-break: keep-all;
    box-shadow: 0 1px 3px #0000000d;
}

.rewards.success {
    background-color: rgba(74, 144, 226, 0.15);
    border: 1px solid rgba(74, 144, 226, 0.3);
}

.rewards.fail {
    background-color: rgba(229, 57, 53, 0.15);
    border: 1px solid rgba(229, 57, 53, 0.3);
}

.rewards .title {
    font-size: 0.8rem;
    font-weight: 700;
}

.rewards.success .title {
    color: #4a90e2;
}

.rewards.fail .title {
    color: #e53935;
}

.choice-rewards {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    gap: 10px;

    width: 100%;
}

.choice {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    flex-grow: 1;
    gap: 10px;

    width: 100%;
}

.choice-name {
    padding: 3px 0;

    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: 4px;

    font-size: 0.9rem;
    font-weight: 700;
    text-align: center;
    word-break: keep-all;
}

.choice-name.success {
    background-color: rgb(227, 242, 253);
    color: rgb(21, 101, 192);
}

.choice-name.fail {
    background-color: rgb(252, 228, 236);
    color: rgb(194, 24, 91);
}

[data-theme="dark"] {
    .event {
        background: rgba(255, 255, 255, 0.05);
    }

    .rewards {
        background: rgba(255, 255, 255, 0.1);
        border-color: rgba(255, 255, 255, 0.1);
    }

    .rewards.success {
        background-color: rgba(144, 202, 249, 0.15);
        border: 1px solid rgba(144, 202, 249, 0.3);
    }

    .rewards.fail {
        background-color: rgba(239, 83, 80, 0.15);
        border: 1px solid rgba(239, 83, 80, 0.3);
    }

    .rewards.success .title {
        color: #90caf9;
    }

    .rewards.fail .title {
        color: #ef5350;
    }
}
