.journey-container {
    padding: 20px;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;

    width: 100%;
    max-width: 1200px;
}

.search-box-wrapper {
    display: flex;

    width: 100%;
    max-width: 600px;
}

.checkboxes {
    display: flex;
    flex-direction: row;
    gap: 20px;

    font-size: 0.9rem;
    color: var(--text-color);
}

.checkbox {
    display: flex;
    flex-direction: row;
    gap: 10px;

    cursor: pointer;
}

.checkbox * {
    pointer-events: none;
}

.tabs {
    display: flex;
    flex-direction: row;
    gap: 10px;
}

.tab-button {
    padding: 8px 16px;

    background: var(--card-bg);
    border: 1px solid var(--banner-active);
    border-radius: 8px;

    font-weight: 700;
    color: var(--text-color);

    cursor: pointer;
    transition: all 0.2s;
}

.tab-button.active {
    background: var(--banner-active);
    border-color: var(--banner-active);

    color: white;
}

.legends {
    padding: 5px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    gap: 10px;

    /* align-self: flex-start; */

    background: var(--card-bg);
    border: 1px solid var(--border-color);
    border-radius: 8px;

    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.legend {
    display: flex;
    flex-direction: row;
    gap: 5px;

    font-size: 0.9rem;
    color: var(--text-color);
}

.legend .box {
    width: 20px;
    height: 20px;

    background: var(--result-bg);
    border: 1px solid var(--border-color);
    border-radius: 4px;
}

.legend.success .box {
    background: rgba(60, 150, 110, 0.35);
    border: 1px solid rgba(60, 150, 110, 0.35);
}

.legend.fail .box {
    background: rgba(180, 90, 90, 0.24);
    border: 1px solid rgba(180, 90, 90, 0.32);
}

.journey-info-wrapper {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 10px;

    width: 100%;
    /* min-height: 80vh; */
}

@media (max-width: 768px) {
    .tabs {
        flex-wrap: nowrap;
        justify-content: flex-start;
        gap: 6px;

        overflow-x: auto;
        -webkit-overflow-scrolling: touch;
    }

    .tab-button {
        padding: 6px 10px;

        flex-shrink: 0;

        font-size: 0.9rem;
    }
}
