.savior-container {
    margin: 0 auto;
    padding: 20px;

    display: flex;
    flex-direction: column;
    gap: 15px;

    width: 100%;
    max-width: 1200px;
    min-height: 80vh;
}

.filter-wrapper {
    position: relative;

    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 15px;

    width: 100%;

    z-index: 1;
}

.saviors {
    display: grid;
    gap: 5px;
    grid-template-columns: repeat(auto-fill, minmax(110px, 1fr));

    width: 100%;
}

@media (max-width: 768px) {
    .filter-wrapper {
        justify-content: space-between;
        flex-wrap: nowrap;
        gap: 8px;
    }

    .saviors {
        /* grid-template-columns: repeat(auto-fill, minmax(85px, 1fr)); */
    }
}

@media (max-width: 600px) {
    .savior-container {
        padding: 10px;
    }

    .saviors {
        /* grid-template-columns: repeat(auto-fill, minmax(75px, 1fr)); */
    }
}
