.arcana-container {
    margin: 0 auto;
    padding: 20px;

    display: flex;
    flex-direction: column;
    gap: 15px;

    width: 100%;
    max-width: 1200px;
    min-height: 80vh;
}

.filters-wrapper,
.filters,
.search-boxes {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.filters-wrapper {
    z-index: 1;
}

.search-boxes {
    width: 100%;
    max-width: 600px;
}

.arcana-list-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 0.65rem;

    width: 100%;
}

.checkers {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
}

@media (max-width: 768px) {
    /* .filters-wrapper {
        flex-direction: column;
    } */

    /* .filters {
        order: 2
    } */

    .arcana-list-container {
        grid-template-columns: repeat(auto-fill, minmax(130px, 1fr));
        gap: 1rem;
    }
}

@media (max-width: 600px) {
    .arcana-container {
        padding: 10px;
    }

    .arcana-list-container {
        grid-template-columns: repeat(auto-fill, minmax(115px, 1fr));
        gap: 0.5rem;
    }
}
