.deck-changer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 10px;

    width: 100%;

    font-size: 1rem;
    color: var(--text-color);
}

.deck-changer button {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    width: 27px;
    height: 27px;

    font-size: 27px;
    color: var(--text-color);

    cursor: pointer;
    transition: 0.3s;
}

.deck-changer button:hover {
    color: var(--primary-color);
    transform: scale(1.2);
}

.deck-changer button.cancel-button:hover {
    color: rgba(255, 0, 0, 0.4);
}

.deck-changer button.disabled {
    display: none;
}

.deck-changer button.arrow {
    position: relative;
    top: 1px;
}

.deck-changer button i {
    width: 100%;
    height: 100%;
}

.deck-names-wrapper {
    position: relative;

    padding: 0 10px;

    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;

    max-width: 300px;

    overflow: hidden;
}

.deck-names {
    display: flex;

    width: fit-content;
    height: fit-content;
}

.deck-name {
    min-width: 280px;
    max-width: 280px;

    font-size: 1.3rem;
    font-weight: 700;
    text-align: center;
    word-break: keep-all;
    white-space: nowrap;
}

.deck-name.disabled {
    opacity: 0;
}

.deck-name-change-input {
    position: absolute;

    display: none;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;

    z-index: -1;
}

.deck-name-change-input.active {
    padding-right: 10px;

    display: flex;

    z-index: 1;
}

.deck-name-change-input input {
    padding-right: 10px;

    width: 100%;
    height: 100%;

    background: transparent;
    border: 1px solid var(--text-color);
    border-radius: 4px;

    font-size: 1.3rem;
    font-weight: 700;
    color: var(--text-color);
    text-align: center;
}

.deck-name-change-buttons {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 8px;

    min-width: 62px;
}

[data-theme="dark"] {
    .deck-name-change-input input {
        background: rgba(255, 255, 255, 0.1);
    }
}
