.reward {
    display: flex;
    flex-direction: column;
    gap: 5px;

    font-weight: 700;

    overflow: hidden;
    transition: 0.3s;
}

.reward .desc {
    display: flex;
    flex-direction: row;
}

.reward .desc .desc-row {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.reward .desc .desc-row .potential-icon {
    position: relative;

    width: 17px;
    height: 17px;

    border-radius: 3px;

    overflow: hidden;
}

.reward .desc .desc-row .potential-icon > img.potential-logo {
    position: absolute;
    top: 1px;
    left: 1px;

    width: 15px;
    height: 15px;

    z-index: 1;
}

.reward .desc .desc-row img {
    width: 17px;
    height: 17px;
}

.reward .value-desc {
    font-weight: 400;
}

.reward .desc .plain {
    font-weight: 400;
}

.reward .desc .plus {
    font-weight: 400;
    color: var(--primary-color);
}

.reward .desc .minus {
    font-weight: 400;
    color: #ef5350;
}

.reward .desc .special {
    font-size: 0.9rem;
    font-weight: 400;
    color: var(--primary-color, #90caf9);

    cursor: pointer;
}
