.items-container {
    padding: 20px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;

    width: 100%;
    max-width: 1400px;
}

.filters {
    padding: 0 30px;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 5px;

    width: 100%;
}

.items {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
    gap: 10px;

    width: 100%;
}

.item {
    padding: 10px;

    display: flex;
    flex-direction: column;
    align-items: center;

    background-color: var(--card-bg);
    border: 1px solid var(--border-color);
    border-radius: 10px;

    cursor: pointer;
    overflow: hidden;
    transition: transform 0.2s, box-shadow 0.2s;
}

.item.rare {
    border-color: #2196f3;
}

.item.unique {
    border-color: #9c27b0;
}

.item.legendary {
    border-color: #ff9800;
}

.item:hover {
    transform: translateY(-5px);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.item > img {
    margin-bottom: 10px;

    width: 80px;
    height: 80px;
    object-fit: contain;

    border-radius: 5px;
}

.item .item-name {
    font-size: 0.9rem;
    font-weight: 500;
    line-height: 1.2;
    color: var(--text-color);
    text-align: center;
    word-break: keep-all;
}

@media (max-width: 768px) {
    .filters {
        padding: 0;
    }
}
