.event {
    padding: 10px;

    display: flex;
    flex-direction: column;
    gap: 10px;

    background: var(--card-bg);
    border: 1px solid var(--border-color);
    border-radius: 12px;

    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.event-name {
    font-weight: 700;
    color: var(--primary-color);
}

.event-conditions {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 6px;
}

.condition {
    padding: 2px 5px;

    display: inline-flex;
    align-items: center;
    justify-content: center;

    border: 1px solid transparent;
    border-radius: 20px;

    font-size: 0.8rem;
    font-weight: 700;
}

.condition.time {
    background-color: #ffffff;
    border: 1px solid #bbdefb;

    color: #1565c0;
}

.condition.item {
    background-color: #ffffff;
    border: 1px solid #e57373;

    color: #e57373;
}

.choices {
    display: flex;
    flex-direction: column;
    gap: 10px;

    color: var(--text-color);
}

.choice {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.choice-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.choice-name {
    font-size: 0.9rem;
    font-weight: 700;
}

.reward-wrapper {
    padding: 5px;

    display: flex;
    flex-direction: column;
    /* gap: 3px; */

    background-color: var(--result-bg);
    border: 1px solid var(--input-border);
    border-radius: 8px;

    font-size: 0.85rem;
    color: var(--text-color);
}

.rewards {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.reward-wrapper.success {
    background: rgba(60, 150, 110, 0.35);
    border: 1px solid rgba(60, 150, 110, 0.35);
    /* background: #dcffdd; */
    /* border-color: transparent; */
}

.reward-wrapper.fail {
    background: rgba(180, 90, 90, 0.24);
    border: 1px solid rgba(180, 90, 90, 0.32);
    /* background: #ffd7d7; */
    /* border-color: transparent; */
}

.reward-group {
    display: flex;
    flex-direction: column;
    gap: 3px;
}

.choice-rewards {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    gap: 10px;

    width: 100%;
}

.choice-condition {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    gap: 5px;

    width: 100%;
    height: 20px;
}

.choice-condition .badge {
    padding: 1px 3px;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    background-color: transparent;
    border-radius: 3px;
}

.choice-condition .badge.difficulty {
    background: #e57373;
    color: white;
}

.choice-condition .badge.easy {
    background: rgba(110, 231, 183, 0.25);
    border: 1px solid rgba(110, 231, 183, 0.45);

    color: #333;
}

.choice-condition .badge.normal {
    background: rgba(245, 200, 90, 0.22);
    border: 1px solid rgba(245, 200, 90, 0.4);

    color: #333;

    box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0.06);
}

.choice-condition .badge.item {
    background: rgba(0, 0, 0, 0.15);
    border: 1px solid rgba(180, 200, 255, 0.35);
    color: rgba(0, 0, 0, 0.95);
}

.choice-condition .badge > div {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3px;
}

.choice-condition .badge > div > img {
    height: 18px;

    background: rgba(255, 255, 255, 0.15);
    border: 1px solid rgba(255, 255, 255, 0.68);
    border-radius: 4px;

    box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0.04);
}

.group-divider {
    position: relative;

    height: 17px;

    text-align: center;
}

.group-divider::before,
.group-divider::after {
    content: "";
    position: absolute;
    top: 50%;
    width: 45%;
    height: 1px;
    background: rgba(0, 0, 0, 0.25);
}

.group-divider::before {
    left: 0;
}

.group-divider::after {
    right: 0;
}

.group-divider > span {
    padding: 0 10px;
}

.arcana-choice {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    flex-grow: 1;
    gap: 10px;

    width: 50%;
}

.arcana-choice-name {
    padding: 3px 0;

    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: 4px;

    font-size: 0.85rem;
    font-weight: 700;
    text-align: center;
    word-break: keep-all;
}

.arcana-choice-name.success {
    background-color: rgb(227, 242, 253);
    color: rgb(21, 101, 192);
}

.arcana-choice-name.fail {
    background-color: rgb(252, 228, 236);
    color: rgb(194, 24, 91);
}

[data-theme="dark"] {
    .event {
        background: rgba(255, 255, 255, 0.05);
        border: 1px solid rgba(255, 255, 255, 0.1);

        color: rgba(229, 231, 235, 0.95);
    }

    .event-name {
        color: rgba(180, 200, 255, 0.95);
    }

    .condition.time {
        background: rgba(255, 255, 255, 0.12);
        border: 1px solid rgba(255, 255, 255, 0.2);

        color: rgba(235, 240, 255, 0.95);
    }

    .choice-condition .badge.item {
        background: rgba(0, 0, 0, 0.28);
        border: 1px solid rgba(180, 200, 255, 0.35);
        color: rgba(210, 225, 255, 0.95);
    }

    .choice-condition .badge.easy {
        color: rgba(220, 255, 240, 0.95);
    }

    .choice-condition .badge.normal {
        color: rgba(255, 245, 215, 0.95);
    }

    .reward-wrapper {
        background: rgba(0, 0, 0, 0.3);
        border: 1px solid rgba(255, 255, 255, 0.1);
    }

    .reward-wrapper.success {
        background: rgba(56, 170, 120, 0.26);
        border: 1px solid rgba(110, 231, 183, 0.3);
    }

    .reward-wrapper.fail {
        background: rgba(165, 70, 105, 0.28);
        border: 1px solid rgba(252, 165, 165, 0.3);
    }

    .reward-wrapper.success {
        color: rgba(110, 231, 183, 0.95);
    }

    .reward-wrapper.fail {
        color: rgba(252, 165, 165, 0.95);
        /* color: black; */
    }

    .choice-condition .badge > div > img {
        background: rgba(0, 0, 0, 0.25);
        border: 1px solid rgba(255, 255, 255, 0.18);
        border-radius: 4px;

        box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0.04);
    }

    .group-divider::before,
    .group-divider::after {
        background: rgba(255, 255, 255, 0.25);
    }

    .group-divider > span {
        color: #aaa;
    }
}
