.nav-button {
    position: absolute;
    top: 50%;

    padding: 0;
    
    display: flex;
    align-items: center;
    justify-content: center;
    
    width: 100px;
    height: 100px;
    
    background: transparent;
    border-radius: 50%;
    transition: background 0.3s;

    font-size: 4rem;
    color: var(--text-color);

    cursor: pointer;
    transform: translateY(-50%);
    z-index: 1;
}

.nav-button:hover {
    background-color: rgba(0, 0, 0, 0.1);
}

.nav-button.prev {
    left: 20px;
}

.nav-button.next {
    right: 20px;
}

@media (max-width: 768px) {
    .nav-button {
        position: fixed;

        width: 80px;
        height: 80px;

        background: transparent;
        border: none;

        font-size: 3rem;
        color: transparent;
    }

    .nav-button.prev {
        left: 10px;
    }

    .nav-button.next {
        right: 10px;
    }
}
