.reward {
    display: flex;
    flex-direction: column;
    gap: 5px;

    font-weight: 400;

    overflow: hidden;
    transition: 0.3s;
}

.reward .desc {
    display: flex;
    flex-direction: row;
}

.reward .desc * {
    word-break: keep-all;
}

.reward .desc .desc-row,
.reward .desc .desc-row-info {
    display: flex;
    flex-direction: row;
    align-items: center;

    word-break: keep-all;
    white-space: nowrap;
}

.reward .desc .desc-row .potential-icon {
    position: relative;

    width: 17px;
    height: 17px;

    border-radius: 3px;

    overflow: hidden;
}

.reward .desc .desc-row .potential-icon > img.potential-logo {
    position: absolute;
    top: 1px;
    left: 1px;

    width: 15px;
    height: 15px;

    z-index: 1;
}

.reward .desc .desc-row .potential-icon > img.stat-potential-logo {
    position: absolute;
    top: 1px;
    left: 0;

    width: 15px;
    height: 15px;

    z-index: 1;
}

.reward .desc .desc-row img {
    padding: 1px;
    height: 17px;

    background: rgba(0, 0, 0, 0.25);
    border: 1px solid rgba(255, 255, 255, 0.65);
    border-radius: 3px;

    box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0.04);
}

.reward .value-desc {
    font-weight: 400;
    word-break: keep-all;
}

.reward .desc .plain {
    font-weight: 400;
}

.reward .desc .plus {
    font-weight: 400;
    color: rgba(30, 40, 60, 0.9);
    /* color: rgba(40, 70, 120, 0.85); */
    /* color: var(--primary-color); */
}

.reward .desc .minus {
    font-weight: 400;
    color: #df6b6a;
}

.reward .desc .special {
    /* font-size: .9rem; */
    font-weight: 600;
    color: rgba(30, 90, 200, 1);
    /* color: rgba(50, 100, 200, 0.95); */
    /* color: var(--primary-color, #90caf9); */
    /* text-decoration: underline dotted; */

    cursor: pointer;
}

[data-theme="dark"] {
    .reward .desc .plus {
        color: rgba(229, 231, 235, 0.92);
    }

    .reward .desc .special {
        color: rgba(229, 231, 235, 0.92);
    }

    .reward .value-desc {
        color: rgba(229, 231, 235, 0.92);
    }
}
