.skill {
    padding: 15px;

    display: flex;
    flex-direction: column;
    gap: 15px;

    background-color: #ffffff;
    border: 1px solid #bbbbbb;
    border-radius: 12px;

    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.03);
    color: #333;
}

[data-theme="dark"] .skill {
    background: rgba(255, 255, 255, 0.03);
    border: none;
    box-shadow: none;
    color: inherit;
}

.skill-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 15px;

    width: 100%;
}

.skill-header img {
    min-width: 60px;
    max-width: 60px;
    height: 60px;

    border-radius: 8px;
    border: 1px solid #bbbbbb;
}

[data-theme="dark"] .skill-header img {
    border: 1px solid var(--border-color);
}

.skill-summary {
    display: flex;
    flex-direction: column;
    gap: 10px;

    width: 100%;
}

.name-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-grow: 1;
}

.skill-name {
    font-size: 1.1rem;
    font-weight: 700;
    color: var(--primary-color);
}

.skill-type {
    padding: 2px 6px;

    background: #eee;
    border: 1px solid #999;
    border-radius: 4px;

    font-size: 0.8rem;
    color: #333;
}

[data-theme="dark"] .skill-type {
    background: #333;
    border: 1px solid #777;
    color: #ccc;
}

.badges {
    display: flex;
    gap: 8px;
}

.badge {
    padding: 2px 6px;

    border-radius: 4px;

    font-size: 0.75rem;
    font-weight: 700;
}

.badge.cooltime {
    color: #1864ab;
    border: 1px solid #a5d8ff;
    background: #e7f5ff;
}

.badge.target {
    color: #e03131;
    border: 1px solid #ffc9c9;
    background: #ffe3e3;
}

.badge.target-ally {
    color: #2b8a3e;
    border: 1px solid #b2f2bb;
    background: #d3f9d8;
}

.badge.nova-gain {
    color: #e67700;
    border: 1px solid #ffec99;
    background: #fff3bf;
}

.badge.nova {
    color: #d9480f !important;
    border-color: #ffc078 !important;
    background: #ffe8cc !important;
    align-self: flex-start;
}

.badge.break {
    background: rgba(100, 100, 100, 0.1);
    color: #333333;
    border: 1px solid #cccccc;
}

.badge.blank {
    height: 20px;
}

.flavor-keyword {
    color: #e67700;
    text-decoration-color: #e67700;
}

[data-theme="dark"] {
    .badge.cooltime {
        background: rgba(100, 200, 255, 0.2);
        color: #8ecfff;
        border: 1px solid rgba(100, 200, 255, 0.4);
    }

    .badge.target {
        background: rgba(255, 100, 100, 0.2);
        color: #ff8e8e;
        border: 1px solid rgba(255, 100, 100, 0.4);
    }

    .badge.target-ally {
        background: rgba(100, 255, 120, 0.2);
        color: #8eff9e;
        border: 1px solid rgba(100, 255, 120, 0.4);
    }

    .badge.break {
        background: rgba(150, 150, 150, 0.2);
        color: #e0e0e0;
        border: 1px solid rgba(200, 200, 200, 0.4);
    }

    .badge.nova-gain {
        background: rgba(255, 165, 0, 0.2);
        color: #ffa500;
        border: 1px solid rgba(255, 165, 0, 0.4);
    }

    .badge.nova {
        color: #ff9f43 !important;
        border-color: #ff9f43 !important;
        background: rgba(255, 159, 67, 0.1) !important;
    }
}

.skill-desc-wrapper {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.skill-desc {
    font-size: 0.9rem;
    line-height: 1.5;
    color: var(--text-color);
    word-break: keep-all;
}

.skill-desc span {
    font-size: 0.9rem;
    color: #e67700;
    text-decoration-color: #e67700;
    text-decoration: underline;
    text-underline-offset: 2px;
    word-break: keep-all;
}

[theme-dark="dark"] .skill-desc span {
    color: #ffd700;
    text-decoration-color: #ffd700;
}

.flavor-texts {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 4px;

    font-size: 0.85rem;
}

.flavor-texts > div {
    word-break: keep-all;
}

.flavor-texts > div span {
    color: #e67700;
    text-decoration-color: #e67700;
    text-decoration: underline;
    text-underline-offset: 2px;
}

[theme-dark="dark"] .flavor-texts > div span {
    color: #ffd700;
    text-decoration-color: #ffd700;
}

.skill-nova {
    padding-top: 15px;

    display: flex;
    flex-direction: column;
    gap: 8px;

    border-top: 1px solid #ddd;
}

[data-theme="dark"] .skill-nova {
    border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.skill-nova .desc {
    font-size: 0.85rem;

    color: #444;
    /* font-style: italic; */
    word-break: keep-all;
}

[data-theme="dark"] .skill-nova .desc {
    color: #ddd;
}

.level-data-wrapper {
    display: flex;
    flex-direction: column;
    gap: 10px;

    width: 100%;
    max-height: 38px;
    overflow: hidden;
    transition: 0.5s;
}

.level-data-wrapper.active {
    max-height: 500px;
}

.show-level-button {
    padding: 10px;

    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;

    width: 100%;

    background-color: #f1f3f5;
    border: 1px solid #999999;
    border-radius: 8px;

    font-weight: 700;
    color: #222222;

    cursor: pointer;
    transition: all 0.3s;
}

[data-theme="dark"] .show-level-button {
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.1);
    color: inherit;
}

.show-level-button:hover {
    background: rgba(255, 255, 255, 0.1);
}

.show-level-button * {
    pointer-events: none;
}

.level-data-wrapper .triangle {
    font-size: 0.8rem;
    transition: 0.3s;
}

.level-data-wrapper.active .triangle {
    transform: rotate(180deg);
}

[data-theme="dark"] .level-data-wrapper.active .show-level-button {
    background: var(--primary-color);
    border-color: var(--primary-color);

    color: #fff;
}

.level-data {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 4px;
}

.level-data .row {
    display: flex;
    flex-direction: row;
    align-items: center;

    width: 100%;

    background: #e7f5ff;
    border-radius: 4px;
    overflow: hidden;
}

[data-theme="dark"] .level-data .row {
    background: rgba(30, 100, 200, 0.3);
}

.level-data .row .level {
    padding: 10px 0;

    display: flex;
    justify-content: center;
    align-items: center;

    min-width: 40px;
    max-width: 40px;
    height: 100%;

    background: #d0ebff;
    border-right: 1px solid #a5d8ff;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;

    font-weight: bold;
    text-align: center;
    color: #1864ab;
}

[data-theme="dark"] .level-data .row .level {
    background: rgba(0, 80, 200, 0.6);
    border: none;

    color: #fff;
}

.level-data .row .value {
    padding: 10px;

    width: 100%;

    border: 1px solid #a5d8ff;
    border-left: none;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;

    font-size: 0.9rem;
    line-height: 1.2;
    word-break: keep-all;
}

[data-theme="dark"] .level-data .row .value {
    border: 1px solid rgba(0, 80, 200, 0.3);
}
