.schedule-container {
    padding: 20px;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 20px;

    width: 100%;
    max-width: 1200px;
}

.tabs,
.destinations {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 10px;

    width: 100%;
}

.tabs button {
    padding: 8px 16px;

    background: var(--card-bg);
    border: 1px solid var(--banner-active);
    border-radius: 8px;

    font-weight: 700;
    color: var(--text-color);

    cursor: pointer;
    transition: all 0.2s;
}

.tabs button.active {
    background: var(--banner-active);
    border-color: var(--banner-active);

    color: white;
}
