.app-footer {
    margin-top: auto;
    padding: 2rem 2rem 1rem 2rem;

    display: flex;
    flex-direction: column;
    align-items: center;

    background-color: transparent;
    border-top: transparent;

    font-size: .9rem;
    color: #888;
    text-align: center;
}

.app-footer.main {
    padding: 2rem 2rem 0 2rem;

    color: var(--text-sub);
}

.app-footer p {
    margin: .5rem 0;

    font-size: 0.8rem;
    
    opacity: 0.8;
    word-break: keep-all;
}

[data-theme=dark] .app-footer p {
    color: #aaa;
    border-color: #444;
}

@media (max-width: 600px) {
    .app-footer {
        padding: 1rem !important;
        transform: none;
    }
}