#header-container {
    position: sticky;
    top: 0;

    width: 100%;

    background: var(--header-bg);
    border-bottom: 1px solid var(--border-color, #333);

    backdrop-filter: blur(10px);
    z-index: 100;
}

.header {
    position: relative;
    margin: 0 auto;
    padding: var(--header-padding);

    display: flex;
    justify-content: space-between;
    align-items: center;

    width: 100%;
    max-width: var(--header-max-width);
    height: var(--header-height);
}

.header-left,
.header-right,
.toggle-group {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.2rem;
}

.hamburger-wrapper {
    position: relative;

    display: flex;
    align-items: center;
    justify-content: center;
}

.header-btn,
.dark-light-toggle {
    padding: 0;

    display: flex;
    justify-content: center;
    align-items: center;

    width: var(--btn-size);
    height: var(--btn-size);

    background: transparent;
    border: 1px solid var(--btn-border-color);
    border-radius: 50%;

    cursor: pointer;
    transition: all 0.2s ease;
}

.header-btn i {
    font-size: var(--btn-font-size);
    color: var(--header-text-color);
}

.icon30 {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    position: absolute;
}
.icon30 > svg {
    transition: fill 0.5s, transform 0.5s ease;
}

.sun {
    display: flex;
}
.moon {
    position: relative;
    top: -1px;

    display: none;
}

[data-theme="dark"] .dark-light-toggle .sun {
    display: none;
}

[data-theme="dark"] .dark-light-toggle .moon {
    display: flex;
}

