.select-wrapper {
    position: relative;

    padding: 10px 16px;

    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 10px;

    min-width: 90px;

    background-color: var(--header-bg) !important;
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 20px;

    font-size: 0.9rem;
    color: var(--text-color);

    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);

    cursor: pointer;
    outline: none;
    transition: border-color 0.3s;
    z-index: 1;
}

.select-wrapper:active,
.select-wrapper:focus,
.select-wrapper:hover {
    border-color: var(--primary-color);
}

.select-wrapper .title {
    font: inherit;
}

.select-wrapper .title.disabled {
    display: none;
}

.select-wrapper .title + .represent {
    width: auto;
}

.select-wrapper .title.disabled + .represent {
    width: 100%;
    text-align: center;
}

.select-wrapper .title,
.select-wrapper .represent {
    word-break: keep-all;
    white-space: nowrap;
}

.select-wrapper .list {
    position: absolute;
    top: 45px;
    right: 0;

    display: none;
    flex-direction: column;
    align-items: flex-start;

    width: 100%;
    max-height: 300px;

    background-color: var(--header-bg);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 10px;

    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);

    transition: 0.3s;
    overflow-y: auto;
}

/* .select-wrapper .list */
.select-wrapper .list::-webkit-scrollbar {
    width: 10px; /*   */
}

.select-wrapper .list::-webkit-scrollbar-thumb {
    height: 30%; /*   */
    background: #b4b9bd; /*   */

    background-clip: padding-box;
    border: 2px solid transparent;
    border-radius: 8px;
}

.select-wrapper .list::-webkit-scrollbar-track {
    background: transparent; /*   */
    border-radius: 4px;
}

.select-wrapper .list .element {
    padding: 7px;

    width: 100%;
    min-height: fit-content;

    text-align: center;
    word-break: keep-all;
    overflow: hidden;
}

.select-wrapper .list .element:hover {
    background-color: var(--btn-hover-bg);
}

.select-wrapper .list.active {
    display: flex;
}

@media (max-width: 768px) {
    .select-wrapper {
        padding: 8px 7px;

        flex: 1;

        /* min-width: fit-content; */
        min-width: 75px;
        height: 40px;

        font-size: 0.8rem;
    }
}

@media (max-width: 600px) {
    .select-wrapper {
        font-size: 0.8rem;
    }
}
