.nav-item {
    padding: 0.6rem;

    display: flex;
    align-items: center;
    gap: 0.8rem;
    
    border-radius: 6px;

    font-size: 0.95rem;
    color: var(--header-text-color);
    text-decoration: none;
    white-space: nowrap;
    
    transition: background 0.2s;
    cursor: pointer;
}

.nav-item:hover {
    background-color: var(--btn-hover-bg);
}

.nav-item .icon {
    width: 24px;
    height: 24px;
    
    font-size: 1.2rem;
    text-align: center;
}

.nav-item .icon img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    filter: drop-shadow(0 0 5px rgba(255, 255, 255, 0.9)) brightness(1.4);
}