.illustration {
    padding: 0;

    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1;

    min-height: 300px;
}

.illustration img {
    width: 100%;
    max-width: 100%;
    max-height: 80vh;
}

@media (max-width: 768px) {
    .illustration {
        display: none;
    }
}