.checker {
    display: flex;
    gap: 5px;

    font-size: 0.9rem;
    color: var(--text-color);

    cursor: pointer;
}

.checker * {
    pointer-events: none;
}

.checker i {
    font-size: 1.1rem;
    color: var(--text-color);

    transition: .3s;
}

.checker span {
    user-select: none;
}