.main {
    position: relative;
    padding: 2rem;

    width: 100%;
    max-width: 1400px;
    min-height: 100vh;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;

    z-index: 10;
}

.logo-container {
    position: absolute;
    top: 40px;
    left: 50px;
    z-index: 20;
    text-decoration: none;
    display: block;
}

.logo {
    width: 280px;
    height: auto;
    opacity: 1;
    transition: 0.3s;
    filter: drop-shadow(0 0 15px rgba(59, 130, 246, 0.6));
}

.logo:hover {
    transform: scale(1.05);
    filter: drop-shadow(0 0 25px rgba(59, 130, 246, 0.8));
}

@keyframes fadeUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.splash-container {
    position: relative;
    margin: 3rem 0 1.5rem 0;

    text-align: center;
    animation: fadeUp 1s ease-out;
}

@keyframes glowPulse {
    0%,
    100% {
        filter: drop-shadow(0 0 30px rgba(59, 130, 246, 0.4));
        transform: scale(1);
    }

    50% {
        filter: drop-shadow(0 0 50px rgba(6, 182, 212, 0.7));
        transform: scale(1.02);
    }
}

.splash-image {
    margin: 0 auto;

    max-width: 100%;

    animation: glowPulse 3s infinite ease-in-out;
    transition: .3s;
}

.splash-container > .title {
    margin: 0.5rem auto 0;
    padding: 1rem 3rem;

    width: fit-content;
    
    background: rgba(15, 23, 42, 0.6);
    border-radius: 50px;
    border: 1px solid rgba(255, 255, 255, 0.2);

    font-size: 1.8rem;
    font-weight: 700;
    color: #ffffff;
    letter-spacing: 0.05em;
    text-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);

    backdrop-filter: blur(10px);
    transition: .3s;
}

.menus {
    margin-bottom: 1rem;

    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 0.5rem;

    width: 100%;
    max-width: 900px;

    perspective: 1000px;
}

@media (max-width: 1024px) {
    .logo {
        width: 200px;
    }
}

@media (max-width: 600px) {
    .logo-container {
        top: 15px;
        left: 15px;
    }

    .logo {
        width: 180px;
    }

    .splash-container {
        margin-top: 3rem;
        margin-bottom: 2rem;
    }

    .main .title {
        margin: 1.5rem auto 0;
        padding: 0.7rem 1.7rem;

        background: rgba(15, 23, 42, 0.4);
        border-width: 0.5px;

        font-size: 1.1rem;
    }

    .splash-image {
        width: 90%;
        max-width: 350px;
    }
}