.weather-description,
.arcana-description {
    padding: 10px;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    width: 100%;
}

.arcana-description {
    gap: 10px;
}

.arcana-description > .title {
    padding-bottom: 10px;

    width: 100%;

    font-size: 1.2rem;
    font-weight: 700;
    color: var(--primary-color);
    text-align: center;
}

.deck-changer-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;

    width: 100%;
}

.deck-changer-width-controller {
    width: 80%;
}
