.savior {
    position: relative;

    padding: 10px;

    display: flex;
    flex-direction: column;
    align-items: center;

    /* width: 100px; */

    border-radius: 12px;

    cursor: pointer;
    overflow: visible;
    transition: transform 0.2s;
    box-shadow: none;
}

.savior .name {
    font-size: 0.9rem;
    font-weight: 700;
    color: var(--text-color);
    text-align: center;
    word-break: keep-all;
}

.savior .title {
    font-size: 0.7rem;
    font-weight: 700;
    color: var(--text-color);
    text-align: center;
    word-break: keep-all;
}

.savior:hover .name {
    filter: brightness(1.2);
}

.portrait {
    position: relative;

    margin-bottom: 2px;

    width: 80px;
    height: 80px;

    border: 3px solid transparent;
    border-radius: 15px;

    object-fit: cover;
    transition: box-shadow 0.3s;
    overflow: hidden;
}

.portrait > img {
    width: 100%;
}

.portrait.sun {
    border-color: #ff6b6b;
    box-shadow: 0 0 5px rgba(255, 107, 107, 0.5);
}
.portrait.moon {
    border-color: #6b8cff;
    box-shadow: 0 0 5px rgba(107, 140, 255, 0.5);
}

.portrait.star {
    border-color: #6bff8c;
    box-shadow: 0 0 5px rgba(107, 255, 140, 0.5);
}

.portrait.order {
    border-color: #ffd700;
    box-shadow: 0 0 5px rgba(255, 215, 0, 0.5);
}

.portrait.chaos {
    border-color: #d06bff;
    box-shadow: 0 0 5px rgba(208, 107, 255, 0.5);
}

.savior:hover .portrait.sun {
    box-shadow: 0 0 15px #ff6b6b;
    /* transform: scale(1.1); */
}

.savior:hover .portrait.moon {
    box-shadow: 0 0 15px #6b8cff;
    /* transform: scale(1.1); */
}

.savior:hover .portrait.star {
    box-shadow: 0 0 15px #6bff8c;
    /* transform: scale(1.1); */
}

.savior:hover .portrait.order {
    box-shadow: 0 0 15px #ffd700;
    /* transform: scale(1.1); */
}

.savior:hover .portrait.chaos {
    box-shadow: 0 0 15px #d06bff;
    /* transform: scale(1.1); */
}

.class-overlay {
    position: absolute;
    top: 0;
    left: 0;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 25px;
    height: 25px;
}

.class-overlay img {
    width: 100%;
    height: 100%;
}

.class-overlay .class {
    position: absolute;
    top: 5px;
    left: 5px;

    width: 15px;
    height: 15px;

    z-index: 1;
}

@media (max-width: 768px) {
    .savior {
        padding: 5px;
    }
}

@media (max-width: 600px) {
    .savior {
        padding: 0;
    }
}
