.modal-right {
    padding: 2rem;

    display: flex;
    flex-direction: column;
    gap: 20px;
    flex: 1.5;

    max-height: 90vh;

    color: var(--text-color);

    overflow-y: auto;
}

.base-info-wrapper {
    display: flex;
    flex-direction: row;
    gap: 20px;
}

.arcana-image {
    display: none;

    max-width: 200px;
}

.arcana-image img {
    width: 100%;
}

.base-info {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.modal-header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 10px;

    width: 100%;
}

.modal-header h2 {
    font-size: 2rem;
}

.badges {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;

    width: 100%;
}

.rarity-badge {
    padding: 2px 8px;

    background-color: #eee;
    border-radius: 10px;

    font-size: 0.9rem;
    font-weight: 700;
    color: #555;
}

.rarity-badge.SSR {
    background: linear-gradient(135deg, #f2aa4c, #ffc270);
    color: #fff;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
    box-shadow: 0 2px 4px #f2aa4c4d;
}

.rarity-badge.SR {
    background: linear-gradient(135deg, #cb85f7, #e0aaff);
    color: #fff;
    box-shadow: 0 2px 4px #cb85f74d;
}

.char-name {
    font-size: 0.9rem;
    color: #888;
}

.badge {
    padding: 4px 10px;

    border-radius: 20px;

    font-size: 0.9rem;
}

.badge.training {
    background-color: #e3f2fd;
    color: #1565c0;
}

.badge.support {
    background-color: #e8f5e9;
    color: #2e7d32;
}

.group {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.group > h3 {
    padding-left: 10px;

    border-left: 4px solid var(--primary-color);

    font-size: 1.1rem;
    color: var(--text-color);
}

.group-info {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.group-info.row {
    flex-direction: row;
}

.group-info.row > .box {
    flex: 1;
}

.group-info.grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}

.box {
    padding: 0.75rem;

    display: flex;
    flex-direction: column;
    gap: 7px;

    background-color: var(--bg-color);
    border: 1px solid var(--input-border);
    border-radius: 8px;
}

.box > .title {
    font-weight: 700;
    color: var(--primary-color);
}

.box > .desc {
    font-size: 0.8rem;
    color: var(--text-color);
    word-break: keep-all;
}

.events {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

[data-theme="dark"] {
    .box {
        background: rgba(255, 255, 255, 0.05);
    }
}

@media (max-width: 768px) {
    .arcana-image {
        display: block;
    }

    .arcana-image img {
        width: 100%;
    }
}

@media (max-width: 600px) {
    .modal-right {
        padding: 10px;
    }

    .arcana-image {
        display: none;
    }

    .group-info.grid {
        grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
    }
}
