.deck-container {
    padding: 20px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 15px;

    width: 100%;
    max-width: 1200px;
}

.search-boxes {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 10px;

    width: 100%;
    max-width: 600px;

    z-index: 1;
}

.arcana-list-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 0.65rem;

    width: 100%;
}

@media (max-width: 768px) {
    .arcana-list-container {
        grid-template-columns: repeat(auto-fill, minmax(130px, 1fr));
        gap: 0.5rem;
    }
}

@media (max-width: 645px) {
    .deck-container {
        padding: 10px;
    }

    .arcana-list-container {
        grid-template-columns: repeat(auto-fill, minmax(115px, 1fr));
        gap: 0.5rem;
    }
}
