.favorate-button {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;

    /* width: 100%; */
    min-width: 40px;
    max-width: 40px;
    height: 40px;
    width: var(--btn-size);
    height: var(--btn-size);
    
    background: var(--header-bg);
    border: 1px solid var(--btn-border-color);
    border-radius: 50%;
    
    font-size: var(--btn-font-size);
    color: var(--header-text-color);

    cursor: pointer;
    transition: all 0.2s ease;
}

.favorate-button:hover {
    border-color: #ffca28;
    color: #ffca28;
}

.favorate-button.active {
    background-color: #ffca28;
    border-color: #ffca28;
    
    color: white;
    box-shadow: 0 0 10px rgba(255, 202, 40, 0.5);
}