.description-wrapper {
    margin-left: 10px;
    padding: 6px;

    display: flex;
    flex-direction: column;
    gap: 5px;

    border-radius: 6px;
    background: #f8f9fa;
    border: 1px solid #ddd;

    font-size: 0.9rem;
    color: #ddd;
    transition: transform 0.2s, background 0.2s;
}

.description-wrapper > .description {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 5px;

    width: 100%;
}

.description-wrapper .badge {
    padding: 2px 6px;

    background: #555;
    border-radius: 4px;

    /* font-size: 0.75rem; */
    font-weight: bold;
    color: #fff;

    white-space: nowrap;
}

.description-wrapper .badge {
    height: fit-content;

    background: #e0e1e2;
    /* background: #e5e7eb; */
    color: #374151;
    /* color: #374151; */
    /* background: var(--text-color); */
}

.description-wrapper .badge.occur-type {
    background: #fef3c7;
    color: #92400e;
    /* background: #fde68a; */
    /* background: #0984e3; */
}

.description-wrapper .badge.occur-type.random {
    background: #fed7aa;
    /* background: #f59e0b; */
    color: #7c2d12;
    /* background: #e36b09; */
}

.description-wrapper .badge.occur-type.per {
    background: #fca5a5;
    /* background: #e5484d; */
    color: #641414;
    /* background: #e3091b; */
}

.description-wrapper .badge.type.arcana {
    background: #ccfbf1;
    /* background: #0ea5a4; */
    /* background: #10b981; */
    color: #0f766e;
    /* background: #54a570; */
}

.description-wrapper .badge.type.journey {
    background: #e8ebff;
    /* background: #e0e7ff; */
    /* background: #6366f1; */
    color: #3730a3;
    /* background: #6c5ce7; */
}

.description-wrapper .badge.content-name-button {
    /* color: #333; */
    background: transparent;
}

.description-wrapper .contents {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    gap: 10px;
}

.description-wrapper .contents button {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;

    font-size: inherit;
    font-weight: 500;
    color: var(--text-color);
    cursor: pointer;
}

.description-wrapper .contents button i {
    font-size: 12px;
}

.description-wrapper .contents button:hover {
    background-color: var(--text-color);
    color: white;
}

[data-theme="dark"] {
    .description-wrapper {
        background: rgba(0, 0, 0, 0.3);
        border: 1px solid rgba(255, 255, 255, 0.1);
    }

    .description-wrapper .contents button:hover {
        background-color: var(--text-color);
        color: black;
    }
}
