.card {
    position: relative;

    display: flex;
    flex-direction: column;

    background: var(--card-bg);
    border: 1px solid transparent;
    border-radius: 12px;

    cursor: pointer;
    transition: transform 0.2s, box-shadow 0.2s;
    overflow: hidden;
    box-shadow: 0 4px 6px #0000001a;
}

.card:hover {
    border-color: var(--primary-color);

    box-shadow: 0 8px 15px #00000026;
    transform: translateY(-5px);
}

.card-image-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 100%;
    aspect-ratio: 2 / 3;

    background-color: #f9f9f9;

    overflow: hidden;
}

.card-image-wrapper img {
    width: 100%;
    height: 100%;

    object-fit: cover;
    transition: transform 0.3s;
    -webkit-user-drag: none;
    -khtml-user-drag: none;
    -moz-user-drag: none;
    -o-user-drag: none;
}

.card:hover .card-image-wrapper img {
    transform: scale(1.05);
}

.card-summary {
    position: absolute;
    bottom: 0;
    left: 0;

    padding: 50px 0.85rem 0.85rem 0.85rem;

    display: flex;
    flex-direction: column;
    flex-grow: 1;
    gap: 5px;

    width: 100%;

    background: linear-gradient(to top, rgba(0, 0, 0, 0.9) 0%, rgba(0, 0, 0, 0.7) 60%, transparent 100%);
    pointer-events: none;
    box-sizing: border-box;
}

.card-summary h3 {
    font-size: 1.05rem;
    color: white;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.card-header {
    position: absolute;
    top: 0;
    left: 0;

    /* padding: 0.4rem 0.85rem 0.85rem 0.85rem; */
    padding: 0.2rem 0.2rem 0.4rem 0.4rem;

    width: 100%;

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.rarity-badge {
    padding: 2px 8px;

    background-color: #eee;
    border-radius: 10px;

    font-size: 0.8rem;
    font-weight: 700;
    color: #555;
}

.rarity-badge.SSR {
    background: linear-gradient(135deg, #f2aa4c, #ffc270);
    color: #fff;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
    box-shadow: 0 2px 4px #f2aa4c4d;
}

.rarity-badge.SR {
    background: linear-gradient(135deg, #cb85f7, #e0aaff);
    color: #fff;
    box-shadow: 0 2px 4px #cb85f74d;
}

.rarity-badge.R {
    background: linear-gradient(135deg, #60a5fa, #60a5fa);
    color: #fff;
    box-shadow: 0 2px 4px #60a5fa4d;
}

.status {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 32px;
    height: 32px;

    /* padding: 2px 6px;
    
    background-color: var(--bg-color);
    border: 1px solid var(--input-border);
    border-radius: 4px;

    font-size: .8rem;
    font-weight: 700;
    color: var(--primary-color); */
}

.status img {
    width: 100%;
}

.char-name {
    font-size: 0.85rem;
    color: #888;
}

.card.off {
    .card-image-wrapper {
        display: none;
    }

    .card-summary {
        position: static;

        background: var(--card-bg);
    }

    .status {
        position: relative;
        /* right: -7px; */
    }

    .card-summary h3 {
        color: var(--primary-color);
    }

    .favorite-button svg {
        fill: #333;
    }
}

@media (max-width: 768px) {
    .card-summary {
        padding: 50px 0.6rem 0.5rem 0.6rem;
    }

    .card-summary h3 {
        font-size: 0.95rem;
    }

    .card-header {
        padding: 0.2rem 0.2rem 0.4rem 0.4rem;
    }

    .rarity-badge {
        padding: 2px 6px;
    }

    .status {
        width: 25px;
        height: 25px;
    }

    .char-name {
        font-size: 0.75rem;
    }
}

@media (max-width: 600px) {
    .card-summary h3 {
        font-size: 0.85rem;
    }

    .card-header {
        padding: 0.2rem 0.2rem 0.4rem 0.4rem;
    }

    .rarity-badge {
        padding: 2px 6px;
    }

    .status {
        width: 25px;
        height: 25px;
    }

    .char-name {
        font-size: 0.7rem;
    }
}
