.selected-arcana-list {
    position: relative;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 10px;

    width: 100%;
    height: 170px;
}

.selected-arcana-list .selected {
    display: flex;
    justify-content: center;
    align-items: flex-start;

    width: 110px;
    height: 170px;

    background-color: rgba(255, 255, 255, 0.05);
    border: 1px solid var(--primary-color);
    border-radius: 10px;

    font-size: 2rem;
    color: #aaa;

    cursor: pointer;
    overflow: hidden;
}

.selected-arcana-list .selected * {
    pointer-events: none;
}

.selected-arcana-list .selected img {
    width: 100%;
}

.selected-arcana-list .selected i {
    align-self: center;
}

.mini-arcana {
    position: relative;

    display: flex;

    width: 100%;
}

.mini-arcana > .top-overlay {
    position: absolute;
    top: 5px;
    left: 5px;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 3px;
}

.top-overlay .rarity-badge {
    padding: 2px 6px;

    background-color: #eee;
    border-radius: 10px;

    font-size: 0.7rem;
    font-weight: 700;
    color: #555;
}

.top-overlay .rarity-badge.SSR {
    background: linear-gradient(135deg, #f2aa4c, #ffc270);
    color: #fff;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
    box-shadow: 0 2px 4px #f2aa4c4d;
}

.top-overlay .rarity-badge.SR {
    background: linear-gradient(135deg, #cb85f7, #e0aaff);
    color: #fff;
    box-shadow: 0 2px 4px #cb85f74d;
}

.top-overlay .status {
    position: relative;
    left: -4px;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 25px;
    height: 25px;
}

@media (max-width: 1024px) {
}

@media (max-width: 768px) {
}

@media (max-width: 645px) {
    .selected-arcana-list {
        justify-content: flex-start;
        gap: 0;
        transition: none;
    }

    .selected-arcana-list .selected {
        position: absolute;
        background-color: rgb(242, 242, 242);
    }

    .selected-arcana-list .selected + .selected {
        top: 0;
        /* left: 110px; */
        z-index: 1;
    }

    .selected-arcana-list .selected + .selected + .selected {
        top: 0;
        /* left: 220px; */
        z-index: 2;
    }

    .selected-arcana-list .selected + .selected + .selected + .selected {
        top: 0;
        /* left: 330px; */
        z-index: 3;
    }

    .selected-arcana-list .selected + .selected + .selected + .selected + .selected {
        top: 0;
        /* left: 440px; */
        z-index: 4;
    }

    .top-overlay .status {
        left: -2px;
    }
}

[data-theme="dark"] {
    .selected-arcana-list .selected {
        background-color: var(--card-bg);
    }
}
