.star-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -2;
    overflow: hidden;
    background: linear-gradient(to bottom, #020617 0%, #172554 100%);
}

@keyframes starsMove {
    from {
        transform: translateY(0);
    }

    to {
        transform: translateY(-500px);
    }
}

.stars {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: radial-gradient(1.5px 1.5px at 20px 30px, #e0f2fe, rgba(0, 0, 0, 0)), radial-gradient(2px 2px at 90px 40px, #ffffff, rgba(0, 0, 0, 0));
    background-repeat: repeat;
    background-size: 300px 300px;
    animation: starsMove 60s linear infinite;
    opacity: 0.8;
}

.stars:nth-child(2) {
    background-size: 500px 500px;
    animation-duration: 100s;
    opacity: 0.5;
}