.item-wrapper {
    padding: 10px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    gap: 20px;

    align-self: flex-start;

    width: 100%;
}

.item-header {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 10px;

    width: 100%;
}

.item-image {
    padding: 20px;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    max-width: 200px;

    border: 1px solid var(--border-color);
    border-radius: 10px;
    background: var(--card-bg);
}

.item-image.small {
    padding: 0;

    max-width: 70px;
}

.item-image.rare {
    border-color: #2196f3;
}

.item-image.unique {
    border-color: #9c27b0;
}

.item-image.legendary {
    border-color: #ff9800;
}

.item-image > img {
    width: 100%;
    height: auto;
    object-fit: contain;
}

.item-header .item-image {
    display: none;
}

.item-summary {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 5px;

    width: 100%;
}

.item-info {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 10px;

    width: 100%;
}

.item-name {
    font-size: 1.8rem;
    font-weight: 700;
    color: #000;
}

.badges {
    padding-bottom: 10px;

    display: flex;
    flex-direction: row;
    justify-self: flex-start;
    align-items: center;
    gap: 10px;

    width: 100%;

    border-bottom: 1px solid #e0e0e0;
}

.badge {
    padding: 4px 12px;

    background-color: #555;
    border-radius: 15px;

    font-size: 0.85rem;
    font-weight: 600;
    color: #fff;
}

.badge.charm {
    background-color: #7e57c2;
}

.badge.food {
    background-color: #ff7043;
}

.badge.book {
    background-color: #42a5f5;
}

.badge.price {
    background-color: #ffd700;
    color: #000;
}

.badge.pp {
    background-color: #ba68c8;
}

.badge.target {
    background-color: #4db6ac;
}

.description-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-self: flex-start;
    gap: 10px;

    width: 100%;
}

.description-wrapper > .title {
    padding-left: 12px;

    border-left: 5px solid #7e57c2;

    font-size: 1.2rem;
    color: #000;
    line-height: 1.2;
}

.description-wrapper .description {
    padding: 12px 15px;

    background: rgba(0, 0, 0, 0.05);
    border-radius: 8px;

    width: 100%;

    font-size: 0.95rem;
    color: var(--text-color);
    line-height: 1.6;
    word-break: keep-all;
}

.description-wrapper .description.buff {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 5px;
}

.description-wrapper .description .row {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    gap: 10px;
}

.description-wrapper .description img {
    width: 25px;
}

[data-theme="dark"] {
    .item-name,
    .description-wrapper > .title {
        color: var(--text-color);
    }

    .description-wrapper .description {
        background: rgba(255, 255, 255, 0.05);
    }
}

@media (max-width: 768px) {
    .item-wrapper > .item-image {
        display: none;
    }

    .item-header .item-image {
        display: flex;
    }

    .badges {
        border: none;
    }
}
