.potential {
    padding: 8px;
    
    display: flex;
    flex-direction: column;
    
    max-height: 35px;

    background-color: #f8f9fa;
    border: 1px solid #ccc;
    border-radius: 6px;
    
    overflow: hidden;
    transition: .5s;
}

[data-theme="dark"] .potential {
    background: rgba(255, 255, 255, 0.03);
    border: none;
}

.potential.active {
    max-height: 100px;
}

.potential-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
}

.level {
    padding: 2px 6px;

    background: #444;
    border-radius: 4px;

    font-size: 0.8rem;
    font-weight: 700;
    color: white;
}

.desc.keyword {
    font-size: 0.9rem;
    color: #e67700;
    text-decoration-color: #e67700;
    text-decoration: underline;
    text-underline-offset: 2px;
    
    transition: color 0.2s;
    cursor: pointer;
}

[data-theme="dark"] {
    .desc.keyword {
        color: #ffd700;
    }
    
    .desc.keyword:hover {
        color: #fff;
    }

    .flavor-text {
        color: white;
    }
}

.desc.keyword:hover {
    color: #d9480f;
}

.flavor-text {
    margin-top: 10px;

    width: 100%;
    max-height: 0;

    font-size: 0.85rem;
    color: #333;
    line-height: 1.4;
    overflow: hidden;
    transition: .5s;
}

.potential.active .flavor-text {
    max-height: 100px;
}