.hurdle,
.turns {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;

    width: 100%;
    /* max-width: 960px; */
    overflow: hidden;
}

.hurdle {
    max-height: 24px;
}

.hurdle.active {
    max-height: 1500px;
}

.turns {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.hurdle-title {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 10px;

    width: 100%;

    font-size: 20px;
    font-weight: 700;
    color: var(--primary-color);

    cursor: pointer;
}

.hurdle-title .triangle {
    pointer-events: none;
}

.hurdle.active .triangle {
    transform: rotate(180deg);
}

.turn {
    padding: 10px;

    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 20px;

    width: 100%;
    height: 100%;

    background: #fff;
    border: 1px solid #ddd;
    border-radius: 12px;

    transition: all 0.3s ease;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
}

.turn .check {
    width: 20px;
    height: 20px;
    cursor: pointer;
}

.turn .check i {
    font-size: 20px;
    color: var(--text-color);
    pointer-events: none;
}

.turn-info {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 10px;
}

.turn-info > .title {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    gap: 15px;

    color: var(--text-color);
}

.turn-info > .title .turn-count {
    font-size: 1.1rem;
    font-weight: bold;
}

.turn-info > .title .turn-time {
    font-size: 0.9rem;
    color: #888;
}

.turn-description {
    padding-left: 10px;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 10px;

    width: 100%;
}

.description-title {
    font-size: 1rem;
    font-weight: 700;
    color: var(--text-color);
}

[data-theme="dark"] {
    .turn {
        background: rgba(255, 255, 255, 0.05);
        border: 1px solid rgba(255, 255, 255, 0.1);
    }
}

@media (max-width: 768px) {
    .turns {
        grid-template-columns: repeat(1, 1fr);
    }
}
